import React from 'react';
import { Tooltip, Divider, Button, Modal, Input,message } from 'antd';
import { Toolbar, Command,withPropsAPI  } from 'gg-editor';
import styles from './index.scss';
import iconfont from '../../theme/iconfont.scss';

class FlowToolbar extends React.Component {
  constructor(props){
    super(props);
      this.state = {
        content_htmldata:"",
        visible:false,
        inputValue:'',
      }
  }

  componentDidMount() {
    let _this = this;
    // 接收父级传过来的回显JSON 
    window.addEventListener('message',function(event){
      let evenData = event.data;
      if (evenData.type && evenData.type === 'loadJSON') {
        let readData = evenData.data;
        _this.props.propsAPI.read(readData)
        _this.setState({
          inputValue:evenData.name
        })
      }
    })

  }

  saveFlow = () =>{
    let content = this.props.propsAPI.save();
    let sendData = {
      type: 'saveJSON',
      data: content,
      name:this.state.inputValue
    }
    window.top.postMessage(sendData, '*');
  }

  function_ = () => {
    let canvas = document.getElementsByTagName('canvas');
    let cav = canvas[0];
    this.setState({
      content_htmldata: cav.toDataURL("image/png")
    },()=>{
      message.info('图片保存成功')
    })
    let content = cav.toDataURL("image/png");
    
    let sendData = {
      type: 'saveImage',
      data: content
    }
    window.top.postMessage(sendData, '*');
  }

  showImg = () => {
     let canvas = document.getElementsByTagName('canvas');
     let cav = canvas[0];
     this.setState({
       visible:true,
       content_htmldata: cav.toDataURL("image/png")
     })
  }


  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  }


  render() {
    return (
      <div>
        <Toolbar className={styles.toolbar}>
          <Command name="undo">
            <Tooltip title="撤销" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconUndo}`} />
            </Tooltip>
          </Command>
          <Command name="redo">
            <Tooltip title="重做" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconRedo}`} />
            </Tooltip>
          </Command>
          <Divider type="vertical" />
          <Command name="copy">
            <Tooltip title="复制" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconCopyO}`} />
            </Tooltip>
          </Command>
          <Command name="paste">
            <Tooltip title="粘贴" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconPasterO}`} />
            </Tooltip>
          </Command>
          <Command name="delete">
            <Tooltip title="删除" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconDeleteO}`} />
            </Tooltip>
          </Command>
          <Divider type="vertical" />
          <Command name="zoomIn">
            <Tooltip title="放大" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconZoomInO}`} />
            </Tooltip>
          </Command>
          <Command name="zoomOut">
            <Tooltip title="缩小" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconZoomOutO}`} />
            </Tooltip>
          </Command>
          <Command name="autoZoom">
            <Tooltip title="适应画布" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconFit}`} />
            </Tooltip>
          </Command>
          <Command name="resetZoom">
            <Tooltip title="实际尺寸" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconActualSizeO}`} />
            </Tooltip>
          </Command>
          <Divider type="vertical" />
          <Command name="toBack">
            <Tooltip title="层级后置" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconToBack}`} />
            </Tooltip>
          </Command>
          <Command name="toFront">
            <Tooltip title="层级前置" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconToFront}`} />
            </Tooltip>
          </Command>
          <Divider type="vertical" />
          <Command name="multiSelect">
            <Tooltip title="多选" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconSelect}`} />
            </Tooltip>
          </Command>
          <Command name="addGroup">
            <Tooltip title="成组" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconGroup}`} />
            </Tooltip>
          </Command>
          <Command name="unGroup">
            <Tooltip title="解组" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconUngroup}`} />
            </Tooltip>
          </Command>
          <Command name="unGroup">
            <Tooltip title="解组" placement="bottom" overlayClassName={styles.tooltip}>
              <i className={`${iconfont.iconfont} ${iconfont.iconUngroup}`} />
            </Tooltip>
          </Command>
          <Divider type="vertical" />
          <Command name="unGroup">
            <Tooltip title="保存" placement="bottom" overlayClassName={styles.tooltip}>
              <Button onClick={()=>{this.saveFlow()}} size="small" style={{fontSize:'12px'}}>保存</Button>
            </Tooltip>
          </Command>
          <Divider type="vertical" />
          
          <Tooltip title="保存图片" placement="bottom" overlayClassName={styles.tooltip}>
            <Button onClick={()=>{this.function_()}} size="small" style={{fontSize:'12px'}}>保存图片</Button>
          </Tooltip>
          <Tooltip title="查看图片" placement="bottom" overlayClassName={styles.tooltip}>
            <Button onClick={()=>{this.showImg()}} size="small" style={{fontSize:'12px',marginLeft:'15px'}}>查看图片</Button>
          </Tooltip>
          <Divider type="vertical" />
              <Input placeholder="请输入流程图名称" value={this.state.inputValue} onChange={(e)=>this.setState({inputValue:e.target.value})} style={{width:'155px',fontSize:'12px'}} size="small"/>
        </Toolbar>


        <Modal 
          title="查看图片"
          visible={this.state.visible}
          onCancel={this.handleCancel}
          width="80%"
          okText="确定"
          cancelText="取消"
        >
          <img src={this.state.content_htmldata ? `${this.state.content_htmldata}`: ''}/>
          {/* <span>123</span> */}
        </Modal>
      </div>
      

    );
  }
}
export default withPropsAPI(FlowToolbar);
